  left: 0;
  width: 100%;
  height: 100%;
  opacity: 0;
  visibility: hidden;
}

.p-modal--2023.is-open--2023,
.p-modal--2022.is-open--2022,
.p-modal--2021.is-open--2021 {
  opacity: 1;
  visibility: visible;
  animation: bgOpenAnime 0.4s ease-in-out;
}

@keyframes bgOpenAnime {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}
